import React from 'react'
import { Link } from 'react-router-dom';
import { Typography } from '@mui/material';

const NotAuthorized = () => {
  return (
    <main>
        <Typography>
                <Link to="/login" ></Link>  <Link to="/register"></Link>
        </Typography>
    </main>
  )
}

export default NotAuthorized